<template>
  <div class="swiper-container" ref="cur">
    <div class="swiper-wrapper" >
      <div
        class="swiper-slide"
        v-for="(slide, index) in imageObj"
        :key="slide.id"
      >
        <img :src="slide.imgUrl"  :class="{active:currentIndex==index}"  @click="changeCurrentIndex(index)"/>
      </div>
    </div>
    <div class="swiper-button-next"></div>
    <div class="swiper-button-prev"></div>
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  name: "ImageList",
  props: ["skuImageList"],
  data(){
    return {
       currentIndex: 0, //默认点选图片 
    }
  },
  computed: {
    imageObj() {
      return this.skuImageList || {};
    },
  },
  methods:{
    changeCurrentIndex(index){
      this.currentIndex= index;  //点选赋值，改变样式
      this.$bus.$emit('getIndex',this.currentIndex);
    }

  },
  watch: {
    //数据一定ok，但不能保证v-for结构已完成
    skuImageList(newValue, oldValue) {
      this.$nextTick(() => {
        //v-for结束后执行
        new Swiper(this.$refs.cur, {
          slidesPerView:3,   //轮播并排显示
          slidesPerGroup:1,  //每次滚动个数

          // 如果需要前进后退按钮
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },


        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.swiper-container {
  height: 56px;
  width: 412px;
  box-sizing: border-box;
  padding: 0 12px;

  .swiper-slide {
    width: 56px;
    height: 56px;

    img {
      width: 100%;
      height: 100%;
      border: 1px solid #ccc;
      padding: 2px;
      width: 50px;
      height: 50px;
      display: block;

      &.active {
        border: 2px solid #f60;
        padding: 1px;
      }

      // &:hover {
      //   border: 2px solid #f60;
      //   padding: 1px;
      // }
    }
  }

  .swiper-button-next {
    left: auto;
    right: 0;
  }

  .swiper-button-prev {
    left: 0;
    right: auto;
  }

  .swiper-button-next,
  .swiper-button-prev {
    box-sizing: border-box;
    width: 12px;
    height: 56px;
    background: rgb(235, 235, 235);
    border: 1px solid rgb(204, 204, 204);
    top: 0;
    margin-top: 0;
    &::after {
      font-size: 12px;
    }
  }
}
</style>